import React, { useState } from 'react';
import { BookOpen, Code } from 'lucide-react';
import { tutorials } from './data/tutorials';

function App() {
  const [activeSection, setActiveSection] = useState('basics');

  return (
    <div className="container">
      <header className="header">
        <h1>
          <BookOpen size={40} style={{ display: 'inline', marginRight: '10px' }} />
          JavaScript Interactive Tutorials
        </h1>
        <p>Learn JavaScript through interactive examples and hands-on practice</p>
        <div style={{ marginTop: '20px', padding: '15px', background: '#e3f2fd', borderRadius: '8px', textAlign: 'center' }}>
          <p style={{ margin: 0, fontSize: '16px', color: '#1565c0' }}>
             <strong>New!</strong> Check out our{' '}
            <a href="/examples/" target="_blank" rel="noopener noreferrer" style={{ color: '#1976d2', fontWeight: 'bold', textDecoration: 'underline' }}>
              Interactive HTML Examples
            </a>
            {' '}to see JavaScript in action on real web pages!
          </p>
        </div>
      </header>

      <div className="content-wrapper">
        <aside className="sidebar">
          <h3>
            <Code size={20} />
            Topics
          </h3>
          {tutorials.map((tutorial) => (
            <button
              key={tutorial.id}
              className={`nav-item ${activeSection === tutorial.id ? 'active' : ''}`}
              onClick={() => setActiveSection(tutorial.id)}
            >
              {tutorial.icon}
              {tutorial.title}
            </button>
          ))}
        </aside>

        <main className="main-content">
          {tutorials
            .filter((t) => t.id === activeSection)
            .map((tutorial) => (
              <div key={tutorial.id} className="tutorial-section">
                <h2>
                  {tutorial.icon}
                  {tutorial.title}
                </h2>
                {tutorial.content}
              </div>
            ))}
        </main>
      </div>
    </div>
  );
}

export default App;
